import React from 'react';
import './PasswordScreen.css';
import makeStyles from "@material-ui/core/styles/makeStyles";
import Container from "@material-ui/core/Container";
import CssBaseline from "@material-ui/core/CssBaseline";
import Avatar from "@material-ui/core/Avatar";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import {LockOutlined} from "@material-ui/icons"
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import {ChooserLoginTest} from "../../functions/Login/Login";
import Backdrop from "@material-ui/core/Backdrop";
import CircularProgress from "@material-ui/core/CircularProgress";
import {MainElement} from "../../functions/Misc/Misc";
import {AddToAccounts} from "../../functions/Login/AddToAccounts";

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright Â© '}
            <Link color="inherit" href="http://kabeersnetwork.dx.am">
                Kabeers Network
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },

}));

const PasswordScreen = (props) => {
    const classes = useStyles();
    const [text, setText] = React.useState({username: '', password: '', username_error: false, password_error: false,});
    const [open, setOpen] = React.useState(false);
    const handleToggle = () => {
        setOpen(!open);
    };

    const LoginHelper = () => {
        handleToggle();
        ChooserLoginTest(text)
            .then(value => value.ok ? (value.json().then((value) => {
                value.password = btoa(value.password);
                AddToAccounts(value);
                return MainElement.setAttribute('choosenAccount', btoa(JSON.stringify(value))), props.callNext()
            })) : null)
            .catch(e => console.log(e))
    };
    document.title = `Sign in to continue to ${MainElement.getAttribute('appName')}`;
    return (
        <Container component="main" maxWidth="xs" className={'mt5 pt-5'}>
            <Backdrop className={classes.backdrop} open={open}>
                <CircularProgress color="inherit"/>
            </Backdrop>
            <CssBaseline/>
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlined/>
                </Avatar>
                <Typography component="h1" variant="h5">
                    Sign in
                </Typography>
                <form className={classes.form}>
                    <TextField
                        variant="filled"
                        margin="normal"
                        required
                        error={text.username_error}
                        fullWidth
                        id="username"
                        label="Email or Username"
                        name="username"
                        autoComplete="username"
                        autoFocus
                        onChange={event => {
                            event.persist();
                            setText(prevState => ({
                                ...prevState,
                                username: event.target.value
                            }))
                        }}
                    />
                    <TextField
                        variant="filled"
                        margin="normal"
                        required
                        error={text.password_error}
                        fullWidth
                        name="password"
                        label="Password"
                        type="password"
                        id="password"
                        autoComplete="current-password"
                        onChange={event => {
                            event.persist();
                            setText(prevState => ({
                                ...prevState,
                                password: btoa(event.target.value)
                            }))
                        }}
                    />
                    <Button
                        onClick={LoginHelper}
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                    >
                        Sign In
                    </Button>
                    <Grid container>
                        <Grid item xs>
                            <Link href="#" variant="body2">
                                Forgot password?
                            </Link>
                        </Grid>
                        <Grid item>
                            <Link href="/auth/create-account" variant="body2">
                                {"Don't have an account? Sign Up"}
                            </Link>
                        </Grid>
                    </Grid>
                </form>
            </div>
        </Container>
    );
};
/*
                    <FormControlLabel
                        control={<Checkbox value="remember" color="primary"/>}
                        label="Remember me"
                    />

            <Box mt={8}>
                <Copyright/>
            </Box>

 */
PasswordScreen.propTypes = {};

PasswordScreen.defaultProps = {};

export default PasswordScreen;
